body,
label,
.checkbox label {
  font-weight: 300;
}
body * {
  box-sizing: border-box;
}
.btn {
  text-decoration: none;
}
.btn-amarelo {
  border: none;
  color: #000000;
  background-color: #ffd500;
  min-width: 154px;
}
.btn-preto {
  border: none;
  color: #fff;
  background-color: #000000;
  min-width: 154px;
}
.btn-preto:hover,
.btn-preto:focus,
.btn-preto:active {
  color: #fff;
}
.btn-cinza {
  border: none;
  color: #000000;
  background-color: #d1d1d1;
  min-width: 154px;
}
.btn-cinza:hover,
.btn-cinza:focus,
.btn-cinza:active {
  color: #000000;
}
.btn-auto-width {
  min-width: 10px;
}
.btn-extra-pequeno {
  padding-top: 8px;
  padding-bottom: 8px;
  font-size: 12px;
  line-height: 12px;
  min-width: 0;
}
.btn-pequeno {
  padding-top: 8px;
  padding-bottom: 8px;
  font-size: 13px;
  line-height: 13px;
}
.btn-meio-medio {
  padding-top: 12px;
  padding-bottom: 10px;
  font-size: 12px;
  vertical-align: middle;
  line-height: 12px;
}
.btn-medio {
  padding-top: 12px;
  padding-bottom: 12px;
  font-size: 12px;
}
.btn-grande {
  font-size: 15px;
  padding-top: 14px;
  padding-bottom: 14px;
}
.btn-estreito {
  width: auto;
  min-width: 0px;
}
.borda-verde {
  border: 1px solid green;
}
.text-right {
  text-align: right;
}
.float-right {
  float: right;
}
.gerar-pdf {
  font-size: 17px;
}
.gerar-pdf h1 {
  font-family: 'Helvetica';
  font-weight: normal;
  font-size: 28px;
  vertical-align: top;
  margin-top: 0;
  margin-bottom: 0;
}
.gerar-pdf h1 .box-img {
  display: inline;
  position: relative;
  float: left;
}
.gerar-pdf h1 .box-img img {
  width: 10%;
  float: left;
  display: inline;
  margin-right: 2%;
}
.gerar-pdf h1 span {
  vertical-align: top;
  width: 88%;
  display: inline;
  line-height: 100%;
  position: relative;
  float: left;
}
.gerar-pdf span.sub-infos {
  position: relative;
  float: left;
  font-size: 15px;
  margin-bottom: 0px;
  margin-left: 13%;
  top: -20px;
  display: block;
  font-family: 'Helvetica';
}
.gerar-pdf .box-post {
  font-family: 'Arial, sans-serif';
}
.gerar-pdf h2.post-titulo {
  font-family: 'Helvetica';
  font-weight: normal;
  font-size: 20px;
  margin-bottom: 0px;
}
.gerar-pdf .post-info {
  font-family: 'Helvetica';
  font-size: 11px;
  text-transform: uppercase;
  margin-top: 0px;
  position: relative;
  display: inline;
  margin-left: 1px;
  color: #000;
  letter-spacing: 1.2px;
}
.gerar-pdf .box-img-post-destaque {
  background-color: #f8f8f8;
  text-align: center;
  margin-top: 10px;
  margin-bottom: 15px;
  width: 100%;
}
.gerar-pdf img.img-post-destaque {
  max-width: 100%;
}
.gerar-pdf .galeria-pdf {
  width: 100%;
  border: 1px solid white;
}
.gerar-pdf img.galeria-img-grande {
  min-width: 50%;
  max-width: 100%;
  display: block;
}
/* Sticky footer styles
-------------------------------------------------- */
html {
  position: relative;
  min-height: 100%;
}
/*-------------------------------------------------- */
.footer {
  font-size: 13px;
  font-weight: bold;
  letter-spacing: 0.8px;
}
